.program__wrapper {
  width: 1175px;
  margin: 0 auto;
}
.program__title {
  text-align: center;
  font-size: 23.5px;
  font-weight: normal;
  letter-spacing: normal;
  margin-bottom: 55px;
}
.program__block {
  text-align: center;
  margin-bottom: 45px;
}

.program__link {
  display: inline-block;
  text-align: center;
  min-width: 205px;
  letter-spacing: 1.1px;
  border-radius: 10px;
  padding: 17px 0;
  margin-right: 15px;
  font-size: 22px;
  background-color: $golden-yellow;
  color: $black;
}

.program {
  .day__item-name::before {
    position: absolute;
    top: 0;
    left: -100%;
    content: "";
    width: 100%;
    height: 30px;
    background-color: $golden-yellow;
  }
}
