@font-face {
  font-family: "OCRF-Regular";
  src: url("../fonts/ocrf-regularc.otf") format("opentype");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 22px;
  line-height: 1.1;
  font-family: "OCRF-Regular", "Helvetica", "Arial", sans-serif; }

*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

input,
textarea,
select,
button {
  font: inherit; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  width: 950px;
  margin: 0 auto; }

.page-main__promo {
  margin-bottom: 72px; }

.page-main__program {
  margin-bottom: 55px; }

.page-main__features {
  margin-bottom: 55px; }

.page-main__speaker {
  margin-bottom: 55px; }

.page-main__registration {
  margin-bottom: 150px; }

.header {
  padding: 25px 40px 4px 63px; }

.header__wrapper {
  display: flex; }

.header__logo {
  margin-right: auto;
  display: flex;
  align-items: center; }

.header__logo-link:first-child {
  margin-right: 26px; }

.main-nav__list {
  list-style: none;
  display: flex; }

.main-nav__item:not(:last-child) {
  margin-right: 35px; }

.main-nav__link {
  color: #000000;
  font-size: 24px; }
  .main-nav__link:hover {
    opacity: 0.7; }

.promo {
  background-color: #fee600; }

.promo__wrapper {
  background: url("../img/promo.png") no-repeat;
  height: 501px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  padding-bottom: 15px; }

.promo__link {
  display: inline-block;
  text-align: center;
  min-width: 205px;
  letter-spacing: 1.2px;
  border-radius: 10px;
  padding: 15px 0;
  margin-right: 15px;
  font-size: 22px;
  background-color: #2b2d33;
  color: #ffffff; }

.about__decription {
  margin-bottom: 40px; }

.about .decription__block::before {
  content: "";
  position: absolute;
  left: -100%;
  top: 31px;
  width: 100%;
  height: 28px;
  background-color: #000000; }

.about__features {
  margin-bottom: 83px; }

.about__for-whom {
  margin-bottom: 111px; }

.decription__wrapper {
  padding-left: 155px;
  padding-right: 45px; }

.decription__block {
  position: relative;
  background-color: #fee600;
  border-radius: 40px;
  padding: 12px 29px; }
  .decription__block:nth-child(2) {
    margin-top: 16px;
    padding-bottom: 40px; }

.decription__text {
  font-size: 24px;
  line-height: 25px; }
  .decription__text span {
    background-color: #000000;
    color: #ffffff;
    padding-top: 7px;
    position: relative; }
    .decription__text span::before {
      content: "";
      position: absolute;
      left: -30px;
      top: 0;
      width: 30px;
      height: 28px;
      background-color: #000000;
      z-index: 9; }

.features {
  background-color: #2b2d33; }

.features__wrapper {
  padding-top: 18px;
  padding-left: 80px;
  padding-bottom: 46px; }

.features__wrapper > .features__title {
  font-size: 24px;
  font-weight: normal;
  text-align: center;
  margin-top: 25px;
  margin-bottom: 65px; }

.features__list {
  list-style: none;
  max-width: 780px; }

.features__item {
  margin-bottom: 25px; }

.features__title {
  font-weight: normal;
  color: white;
  letter-spacing: normal;
  text-transform: uppercase;
  margin: 0; }
  .features__title span {
    color: #fee600; }

.features__text {
  line-height: 1.1;
  letter-spacing: 1.1px;
  color: #fee600;
  margin: 0; }
  .features__text span {
    color: #ffffff; }

.page-main__features .features__wrapper {
  padding-left: 130px; }

.page-main__features .features__item {
  margin-bottom: 50px; }

.for-whom__title {
  text-transform: uppercase;
  text-align: center;
  font-size: 24px;
  margin-bottom: 50px; }

.for-whom__list {
  width: 640px;
  list-style: none;
  margin-left: auto;
  padding: 0; }

.for-whom__item {
  position: relative;
  margin-bottom: 3px; }
  .for-whom__item::before {
    position: absolute;
    left: -25px;
    top: 15px;
    content: "";
    width: 9px;
    height: 9px;
    background-color: #000000; }

.for-whom__text {
  max-width: 600px;
  font-size: 24px;
  line-height: 1;
  margin: 0; }

.program__wrapper {
  width: 1175px;
  margin: 0 auto; }

.program__title {
  text-align: center;
  font-size: 23.5px;
  font-weight: normal;
  letter-spacing: normal;
  margin-bottom: 55px; }

.program__block {
  text-align: center;
  margin-bottom: 45px; }

.program__link {
  display: inline-block;
  text-align: center;
  min-width: 205px;
  letter-spacing: 1.1px;
  border-radius: 10px;
  padding: 17px 0;
  margin-right: 15px;
  font-size: 22px;
  background-color: #fee600;
  color: #000000; }

.program .day__item-name::before {
  position: absolute;
  top: 0;
  left: -100%;
  content: "";
  width: 100%;
  height: 30px;
  background-color: #fee600; }

.day {
  text-align: center; }

.day__title {
  font-size: 24px;
  font-weight: normal;
  display: inline-block;
  border-radius: 10px;
  background-color: #000000;
  color: #fee600;
  padding: 25px 36px 25px 50px;
  text-align: center;
  margin-bottom: 37px; }

.day__list {
  list-style: none;
  padding: 0; }

.day__item {
  display: flex;
  height: 48px;
  text-align: right;
  position: relative; }

.day__item-name {
  width: 305px;
  margin: 0;
  margin-right: 40px;
  text-align: right;
  align-self: flex-start; }

.day__item-p {
  font-size: 24px;
  background-color: #fee600;
  padding: 2px 30px 2px 0px;
  margin: 0; }

.day__item-text {
  font-size: 22px;
  text-align: left;
  max-width: 650px;
  margin: 0;
  align-self: flex-end; }

.speaker__wrapper {
  text-align: center; }

.speaker__speaker-wrapper {
  display: flex;
  justify-content: center;
  overflow: hidden;
  position: relative; }

.speaker__title {
  font-size: 18px;
  font-weight: normal;
  letter-spacing: 1.5px;
  display: inline-block;
  border-radius: 10px;
  background-color: #fee600;
  color: #000000;
  padding: 15px 50px;
  text-align: center;
  margin-bottom: 40px; }

.speaker__list {
  display: none;
  padding: 0;
  list-style: none; }
  .speaker__list--active {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-bottom: 50px; }

.speaker__item {
  width: 250px;
  height: 250px;
  position: relative;
  margin-bottom: 70px; }
  .speaker__item::before {
    position: absolute;
    top: -10px;
    right: -20px;
    content: "";
    width: 210px;
    height: 210px;
    border-radius: 50%;
    background-color: #fee600;
    z-index: -1; }

.speaker__img {
  border: 7px dashed #000000;
  border-radius: 50%;
  padding: 5px; }

.speaker__name {
  margin: 0;
  line-height: 1; }

.speaker__work {
  opacity: 0.4; }

.speaker__nav {
  width: 98%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: space-between; }

@media (max-width: 768px) {
  .speaker__nav {
    max-width: 580px; } }

.speaker__prev-btn,
.speaker__next-btn {
  width: 40px;
  height: 40px;
  border: 6px solid #000000;
  cursor: pointer;
  background-color: transparent; }

@media (max-width: 768px) {
  .speaker__prev-btn,
  .speaker__next-btn {
    width: 25px;
    height: 25px;
    border-width: 3px; } }

.speaker__prev-btn {
  transform: rotate(45deg);
  border-right: none;
  border-top: none; }

.speaker__next-btn {
  border-left: none;
  border-top: none;
  transform: rotate(-45deg); }

.speaker__prev-btn:hover,
.speaker__next-btn:hover {
  opacity: 0.5; }

.speaker__dots {
  display: flex;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%); }

@media (max-width: 768px) {
  .speaker__dots {
    bottom: 0; } }

.speaker__dots-item {
  border-radius: 50%;
  width: 30px;
  height: 30px;
  background-color: #b3b3b3;
  cursor: pointer; }
  .speaker__dots-item--active {
    background-color: #2b2d33; }

@media (max-width: 768px) {
  .speaker__dots-item {
    width: 20px;
    height: 20px; } }

.speaker__dots-item:not(:last-child) {
  margin-right: 100px; }

.registration__title {
  text-align: center;
  text-transform: uppercase;
  font-size: 23.5px;
  font-weight: normal;
  letter-spacing: normal;
  margin-bottom: 55px;
  margin-bottom: 0; }

.registration__text {
  text-transform: lowercase;
  text-align: center;
  margin-bottom: 50px; }

.registration__block {
  text-align: center;
  margin-bottom: 100px; }

.registration__block-link {
  display: inline-block;
  text-align: center;
  min-width: 205px;
  letter-spacing: 1.1px;
  border-radius: 10px;
  padding: 15px 0;
  margin-right: 15px;
  font-size: 22px;
  background-color: #fee600;
  color: #000000; }

.form {
  text-align: center; }

.form__wrapper {
  background-color: #fee600;
  border-radius: 40px;
  padding: 65px 20px;
  max-width: 810px;
  margin: 0 auto;
  margin-bottom: 40px; }
  .form__wrapper fieldset {
    border: none;
    text-align: left; }

.form__agreement {
  text-align: center;
  margin-bottom: 80px; }

.form__agreemen-label {
  position: relative;
  padding-left: 40px; }
  .form__agreemen-label::before {
    position: absolute;
    left: 0;
    top: -5px;
    content: "";
    width: 30px;
    height: 30px;
    border: 4px solid #000000; }

.form__agreement-checkbox:checked + .form__agreemen-label::after {
  position: absolute;
  top: 3px;
  left: 8px;
  background: #000000;
  content: "";
  width: 14px;
  height: 14px; }

.form__submit {
  display: inline-block;
  text-align: center;
  min-width: 320px;
  letter-spacing: normal;
  border-radius: 10px;
  padding: 17px 0;
  margin-right: 15px;
  font-size: 22px;
  background-color: #fee600;
  color: #000000;
  border: none;
  cursor: pointer;
  margin: 0 auto; }

.user-data__list {
  list-style: none;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  max-height: 180px;
  max-width: 700px;
  padding: 0; }

.user-data__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
  margin-right: 20px; }

.user-data__label {
  min-width: 100px;
  margin-right: 5px; }

.user-data__input {
  height: 38px;
  max-width: 220px; }

#birthday {
  max-width: 160px;
  font-size: 17px; }

.user-experience__list {
  list-style: none;
  padding: 0; }

.user-experience__title {
  font-weight: normal;
  font-size: 22px;
  text-align: center; }

.user-experience__item {
  margin-bottom: 10px; }

.user-experience__label {
  position: relative;
  padding-left: 50px; }
  .user-experience__label::before {
    position: absolute;
    left: 0;
    top: -5px;
    content: "";
    width: 30px;
    height: 30px;
    border: 4px solid #000000; }

.user-experience__checkbox:checked + .user-experience__label::after {
  position: absolute;
  top: 3px;
  left: 8px;
  background: #000000;
  content: "";
  width: 14px;
  height: 14px; }

.footer {
  display: flex;
  justify-content: space-between;
  position: relative; }
  .footer::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 3px;
    background-color: #b3b3b3; }

.footer__social-list {
  display: flex;
  justify-content: space-between;
  list-style: none;
  padding: 0; }

.footer__social-item:not(:last-child) {
  margin-right: 20px; }
